import {Component, OnInit, ViewChild} from '@angular/core';
import {Parametres, Workpackage} from '../../model';
import {PouchdbService} from '../../services/pouchdb-service/pouchdb.service';
import * as XLSX from 'xlsx';

@Component({
    selector: 'app-parametres',
    templateUrl: './parametres.component.html',
    styleUrls: ['./parametres.component.scss']
})
export class ParametresComponent implements OnInit {

    @ViewChild('fileInput') fileInput;
    editForm = false;
    parametres: Parametres = new Parametres();
    listesDeChoix: Array<string> = [];
    editParameterName: string;
    errorMessage: String;
    errorImportMessage: String;

    constructor(private pouchdbservice: PouchdbService) {

    }

    ngOnInit() {
        this.loadData();
    }

    loadData() {
        this.editForm = false;
        this.pouchdbservice.getParametres().subscribe(
            response => {
                this.listesDeChoix = Object.keys(response.listesDeChoix).sort();
                this.parametres = response;
            },
            err => {
                this.errorMessage = err;
            }
        );

    }

    edit(key: string) {
        this.editForm = true;
        if (this.editForm) {
            this.editParameterName = key;
        }
    }

    cancel() {
        this.editForm = false;
        this.editParameterName = null;
    }

    importProgramme() {
        let fileBrowser = <HTMLInputElement> this.fileInput.nativeElement;
        if (fileBrowser.files && fileBrowser.files[0]) {
            let f: File = fileBrowser.files[0];
            let reader = new FileReader();
            let self = this;
            reader.onload = function (e) {
                /* read workbook */
                let target: any = e.target;
                let bstr: string = target.result;
                const wb: XLSX.WorkBook = XLSX.read(bstr, {type: 'binary'});

                /* grab first sheet */
                const wsname: string = wb.SheetNames[0];
                const ws: XLSX.WorkSheet = wb.Sheets[wsname];

                /* save data */
                let data: Array<Array<string>> = XLSX.utils.sheet_to_json(ws, {header: 1, range: 1});
                let programmes = new Array<Workpackage>();
                for (let line of data) {
                    if (line.length >= 2) {
                        if (line[0]) {
                            let workpackage: Workpackage = new Workpackage();
                            workpackage.nom = line[0];
                            workpackage.numero = line[0].substring(2, line[0].indexOf(":"));
                            workpackage.sousWorkpackages = new Array<Workpackage>();
                            programmes.push(workpackage);
                        }
                        if (line[1]) {
                            let subKey: string = line[1].substring(0, line[1].indexOf("."));
                            let sousWorkpackage: Workpackage = new Workpackage();
                            sousWorkpackage.nom = line[1];
                            sousWorkpackage.numero = line[1].substring(0, line[1].indexOf(" "));
                            for (let parent of programmes) {
                                if (parent.numero == subKey) {
                                    parent.sousWorkpackages.push(sousWorkpackage);
                                }
                            }
                        }
                    }
                }
                self.parametres.programmes = programmes;
                self.pouchdbservice.save(self.parametres).subscribe(
                    response => {
                        console.log(response);
                        self.loadData();
                    },
                    err => {
                        self.errorImportMessage = err;
                    }
                );
            };
            reader.readAsBinaryString(f);

        }
        else {
            this.errorImportMessage = "Veuillez slectionner un fichier";
        }
    }



}



