

export class Collaborateur {
    _id: string;
    _rev: string;
    type: string = Collaborateur.name;
    civilite: string;
    nom: string;
    prenom: string;
    mail: string;
    telephone: string;
    fonction: string;
    enCasUrgence: string;
    adresse: Adresse = new Adresse;
}

export class Contact {
    _id: string;
    _rev: string
    type: string = Contact.name;
    civilite: string;
    nom: string;
    prenom: string;
    mail: string;
    telephone: string;
    relationPrivilegiee: boolean;
    commentaire: string;
    dateRencontre: string;
    liensOrganisme: Array<LienOrganisme> = new Array<LienOrganisme>();
}
export class LienOrganisme {
    mail: string;
    telephone: string;
    organisme: Organisme = new Organisme();
    fonction: string;
}


export class Location {
    latitude: number;
    longitude: number;
}

export class Adresse {
    numero: string;
    rue: string;
    complement: string;
    residence: string;
    boitePostale: string;
    codePostal: string;
    ville: string;
    cedex: string;
    location: Location;
}

export class Organisme {
    _id: string;
    _rev: string;
    type: string = Organisme.name;
    typeOrganisme: string;
    nom: string;
    adresse: Adresse = new Adresse;
    telephone: string;
    web: string;
}


export class Acknowledge {
    ok: string;
    id: string;
    rev: string;
}

export class Workpackage {
    nom : string;
    numero: string;
    sousWorkpackages: Array<Workpackage>;
}

export class Parametres {
    _id: string = Parametres.name;
    _rev: string;
    listesDeChoix: {
        cible: Array<string>;
        typeOrganisme: Array<string>;
        thematique: Array<string>;
        modaliteIS: Array<string>;
        natureRelation: Array<string>;
        typeFinanceurTiers: Array<string>;
        modaliteObtention: Array<string>;
        missionRole: Array<string>;
        secteurs: Array<string>;
        missionFormat: Array<string>;
        missionType: Array<string>;
        modaliteOffre: Array<string>;
        agrement: Array<string>;
        civilite: Array<string>;
        prospectionStatut: Array<string>;
        action: Array<string>;
    }
    programmes: Array<Workpackage>;


    constructor() {
        this.listesDeChoix = {
            cible: new Array<string>(),
            typeOrganisme: new Array<string>(),
            thematique: new Array<string>(),
            modaliteIS: new Array<string>(),
            natureRelation: new Array<string>(),
            typeFinanceurTiers: new Array<string>(),
            modaliteObtention: new Array<string>(),
            secteurs: new Array<string>(),
            missionRole: new Array<string>(),
            missionFormat: new Array<string>(),
            missionType: new Array<string>(),
            modaliteOffre: new Array<string>(),
            agrement: new Array<string>(),
            civilite: new Array<string>(),
            prospectionStatut: new Array<string>(),
            action: new Array<string>()
        };
    }
}

export class MissionSynthese {
    resume: string;
    besoinsIdentifies: string;
    objectifs: string;
    enjeu: string;
    methode: string;
    contraintes: string;
    pistesValorisation: string;
    miseEnRelation: string;
    evolution: string;
}

export class Mission {
    _id: string;
    _rev: string
    nom: string;
    type: string = Mission.name;
    numFacture: string;
    contact: Contact;
    client: Organisme;
    financeur: Organisme;
    cdt: boolean;
    programme: string;
    dateDebut: string;
    dateFin: string;
    nbJours: number;
    missionType: string;
    format: string;
    dateCloture: Date;
    isClosed: boolean = false;
    modaliteObtention: string;
    cible: string;
    thematique: string;
    modaliteIS: string;
    commentaire: string;
    dropboxRepertoire: string;
    restrictionAcces: Array<Collaborateur> = new Array<Collaborateur>();
    liensCollaborateur: Array<LienMissionCollaborateur> = new Array<LienMissionCollaborateur>();
    liensMissions: Array<LienEntreMission> = new Array<LienEntreMission>();
    avancement: string;
    prospectionId: string;
    synthese: MissionSynthese = new MissionSynthese();
}

export class LienMissionCollaborateur {
    role: string;
    collaborateur: Collaborateur = new Collaborateur();
}
export class LienEntreMission {
    typeLien: string;
    mission: Mission = new Mission();
}

export class Prospection {
    _id: string;
    _rev: string;
    type: string = Prospection.name;
    nom: string;
    numProspection: string;
    contact: Contact;
    client: Organisme;
    dateContact: string;
    dateCloture: string;
    modaliteObtention: string
    cible: string;
    thematique: string;
    modaliteIS: string;
    commentaire: string;
    montantProposition: string;
    nbJours: string;
    modaliteOffre: string;
    dropboxRepertoire: string;
    statut: string;
    actions: Array<Action>;
    dateChangementStatut: Date;
}

export enum ActionStatut {
    TODO = 'TODO',
    DONE = 'DONE'
}

export class Action {
    _id: string;
    _rev: string;
    type: string = Action.name;
    statut: ActionStatut = ActionStatut.TODO;
    entiteLienType: string;
    entiteLienId: string;
    entiteLienNom: string;
    date: Date;
    typeAction: string;
    collaborateur: Collaborateur = new Collaborateur();
    notificationDate:string;
    notificationType: string;
}



