import {Observable} from 'rxjs/Observable';
import PouchDB from 'pouchdb';
import PouchDBFind from 'pouchdb-find';
PouchDB.plugin(PouchDBFind);
//import * as PouchUpsert from 'pouchdb-upsert'
//PouchDB.plugin(PouchUpsert)

import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {AuthService} from '../auth/auth.service';
// import 'rxjs/add/observable/combineLatest';
// import * as PouchDB from 'pouchdb';

export class PouchDbAdapter {

    private _pouchDB: PouchDB.Database;
    private _remoteCouchDBAddress: string;
    private _pouchDbName: string;

    // rxjs behaviour subjects to expose stats flags
    syncStatus = new BehaviorSubject<boolean>(false);
    couchDbUp = new BehaviorSubject<boolean>(false);
    online$: Observable<boolean>;

    constructor(private authService: AuthService, remoteCouchDBAddress: string, login: string, password: string) {
        this._remoteCouchDBAddress = remoteCouchDBAddress;
        // string function to extract the database name from the URL
        this._pouchDbName = remoteCouchDBAddress
            .substr(remoteCouchDBAddress.lastIndexOf('/') + 1);
        // init local PouchDB
        this.checkRemoteDB();
        

        // new PouchDB(this._pouchDbName).destroy().then(function () {
        //     // database destroyed
        //     console.log('Local DB is Distroyed!!!!!!');

        // }).catch(function (err) {
        //     // error occurred
        // });
        this._pouchDB = new PouchDB(this._pouchDbName);

        let options = {
            live: true,
            retry: true,
            continuous: true,
            auth: {
                username: login,
                password: password
            },
            withCredentials: true
        };

        // do one way, one-off sync from the server until completion
        let self = this;
        this._pouchDB.replicate.from(remoteCouchDBAddress).on('complete', function (info) {
            console.log('complete');
            self.syncStatus.next(true);
            self.couchDbUp.next(true);
            // then two-way, continuous, retriable sync
            self._pouchDB.sync(remoteCouchDBAddress,options)
                .on('paused', info => {
                    console.log('paused');
                    self.checkRemoteDB();
                    self.syncStatus.next(true);
                })
                .on('change', info => {
                    console.log('change');
                    self.syncStatus.next(false);
                })
                .on('error', err => {
                    console.log('PouchDB Error NFCs', err);
                    self.checkRemoteDB();
                    self.syncStatus.next(false);
                    if (err['status'] === 401) {
                        authService.logout();
                    }
                })
        }).on('error', function (info) {
            console.log('error');
            self.syncStatus.next(false);
        });

    }

    clear() {
        this._pouchDB.destroy();
    }

    getDocsOfType(docType: string): Observable<any> {
        return new Observable(observer => {
            this._pouchDB.find({
                selector: {type: docType}
                //                sort: ['name'],
                //                use_index: ['typeDesigndoc', 'typeIndex']
            }).then(function (result) {
                observer.next(result.docs);
            }).catch(function (err) {
                console.log(err);
                observer.error(err);
            });
        });
    }

    getDocsFromSelector(selector: any): Observable<any> {
        return new Observable(observer => {
            this._pouchDB.find(selector).then(function (result) {
                observer.next(result.docs);
            }).catch(function (err) {
                console.log(err);
                observer.error(err);
            });
        });
    }

    // pretty basic and crude function
    // return a Promise with the first 20 docs from allDocs as is
    get20Docs(): Promise<any> {
        return new Promise(resolve => {
            this._pouchDB.allDocs({
                include_docs: true,
                limit: 20
            })
                .then((result) => {
                    resolve(result);
                })
                .catch((error) => {
                    console.log(error);
                });
        });
    }

    post(doc): Observable<any> {
        return Observable.from(this._pouchDB.post(doc));
    }

    put(doc): Observable<any> {
        return Observable.from(this._pouchDB.put(doc));
    }

    get(docId: string): Observable<any> {
        return Observable.from(this._pouchDB.get(docId));
    }

    delete(docId: string, rev: string): Observable<any> {
        return Observable.from(this._pouchDB.put({_id: docId, _rev: rev, _deleted: true}));
    }

    putAttachment(docId: string, rev: string, attachmentId: string, myBase64String: string, contentType: string): Observable<any> {
        return Observable.from(this._pouchDB.putAttachment(docId, attachmentId, rev, myBase64String, contentType));
    }

    getAttachment(docId: string, attachmentId: string): Observable<any> {
        return new Observable(observer => {
            this._pouchDB.getAttachment(docId, attachmentId)
                .then((blob => {
                    var url = URL.createObjectURL(blob);
                    console.log('Stored a png blob of size: ' + url);
                    observer.next(url);
                    //                    URL.revokeObjectURL(url);
                }))
                .catch((error) => {
                    console.log(error);
                    observer.error(error);
                });
        });
    }



    // function to call the below functions
    // then update the rxjs BehaviourSubjects with the 
    // results
    private checkRemoteDB(): void {
        this.checkCouchUp()
            .then((result) => {
                this.couchDbUp.next(result);
            });
    }




    // fairly self explanatory function to make a 
    // GET http request to the URL and return false
    // if an error status or a timeout occurs, true if 
    // successful.
    private checkCouchUp(): Promise<boolean> {
        return new Promise<boolean>((resolve, reject) => {
            let xhr = new XMLHttpRequest();
            xhr.open('GET', this._remoteCouchDBAddress.replace("/" + this._pouchDbName, ""), true);
            xhr.onload = () => {
                if (xhr.status >= 200 && xhr.status < 300) {
                    resolve(true);
                } else {
                    resolve(false);
                }
            };
            xhr.onerror = () => {
                resolve(false);
            };
            xhr.send();
        });
    }




}





